import React, { FC } from "react";
import { graphql, Link, PageProps } from "gatsby";
import { Heading, List, ListItem } from "@chakra-ui/react";

import { Bio, Layout, Seo } from "../components";

const ProjectsPage: FC<PageProps<Queries.ProjectsPageQuery>> = ({ data }) => {
  const projects = data.allMdx.nodes;

  return (
    <Layout title="Projects">
      <Seo title="Projects" />

      <List spacing={4}>
        {projects.map((project) => {
          const title = project.frontmatter?.title || project.fields?.slug;

          return (
            <ListItem key={project.fields?.slug}>
              <article className="post-list-item" itemScope itemType="http://schema.org/Article">
                <header>
                  <h2>
                    <Link to={project.fields?.slug || ""} itemProp="url">
                      <span itemProp="headline">{title}</span>
                    </Link>
                  </h2>
                </header>
                <section>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: project.frontmatter?.description || project.excerpt || "",
                    }}
                    itemProp="description"
                  />
                </section>
              </article>
            </ListItem>
          );
        })}
      </List>
    </Layout>
  );
};

export default ProjectsPage;

export const pageQuery = graphql`
  query ProjectsPage {
    allMdx(sort: { frontmatter: { title: ASC } }, filter: { internal: { contentFilePath: { regex: "//content/projects/.+/" } } }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          title
          description
        }
      }
    }
  }
`;
